<template>
	<div class="card-item" v-if="data.length>0">

		<div class="card" v-show="show" v-for="(item,id) in data" :key="item.id">
			<div class="list-head">
				<span v-text="data[id].shop"></span>
				<span style="color: rgb(239, 56, 99);" v-text="data[id].state"></span>
			</div>
			<div class="list-body">
				<img :src="data[id].img">
				<div class="body-right">
					<div class="row">
						<div class="title" v-text="data[id].title"></div>
						<div>￥ {{data[id].price}}</div>
					</div>
					<div class="row">
						<div>{{data[id].desc}}</div>
						<div>x {{data[id].acount}}</div>
					</div>
				</div>
			</div>
			<div class="list-foot">
				<div class="totel">
					合计￥ <span style="font-weight: bolder;">{{data[id].price}}</span>
				</div>
				<div class="btn-group">
					<button @click="removeCard(item.id)">删除订单</button>
					<button>查看相似</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "OrderList",
		data() {
			return {
				data: [],
				show: true
			}
		},
		mounted() {
			this.$bus.$on('Data', (data) => {
				this.data = data
			})
		},
		methods: {
			removeCard(e) {
				console.log(e)
				this.data.splice(this.data.indexOf(e), e)
			}
		}
	}
</script>

