		font-weight: 500;
		letter-spacing: 1px;
	}

	.list-body {
		margin-top: 15px;
		display: flex;
	}

	.list-body img {
		display: block;
		width: 100px;
		height: 100px;
	}

	.row {
		display: flex;
		-webkit-box-pack: justify;
		justify-content: space-between;
		margin-bottom: 10px;

	}

	.title {
		font-size: 17px;
		color: black;
		font-weight: 600;
